import React, { useEffect } from "react";
import styled from "@emotion/styled";

const Canvas = styled.canvas`
    width: 100%;
    height: 100%;
`;

const dots = [];

let lastTime = 0;
const drawOnCanvas = () => {
    let diff = 0;
    const t = Date.now();
    if (lastTime > 0) {
        diff = t - lastTime;
    }
    lastTime = t;
    const canvas = document.querySelector("canvas"); // hacky
    canvas.width = window.innerWidth;
    canvas.height = window.innerWidth / 2;
    const ctx = canvas.getContext("2d");
    const w = canvas.width;
    const h = canvas.height;
    if (!dots.length) {
        generateDots(w, h);
    }
    ctx.fillStyle = "#002642";
    ctx.fillRect(0, 0, w, h);
    dots.forEach(dot => {
        dot.draw(ctx);
        dot.move(diff);
    });

    requestAnimationFrame(drawOnCanvas);
    // setTimeout(drawOnCanvas, 1000);
}

const palette = [
    [132, 0, 50],
    [0, 38, 66],
    [225, 187, 128],
    [134, 9, 19],
    [131, 144, 115],
]

class Dot {
    constructor(w, h) {
        this.x = Math.random() * w;
        this.y = Math.random() * h;
        this.z = Math.random() * 100;
        this.radius = w / 5;
        this.dx = Math.random() - 0.5;
        this.dy = Math.random() - 0.5;
        this.dz = Math.random() - 0.5;
        this.canvasWidth = w;
        this.canvasHeight = h;

        this.color = palette[Math.floor(Math.random() * palette.length)];
    }

    draw(ctx) {
        ctx.beginPath();
        const depth = this.z / 100;
        ctx.arc(this.x, this.y, this.radius * depth, 0, 2 * Math.PI);
        ctx.fillStyle = "rgba(" + this.color.join(",") + "," + 1 + ")";
        ctx.fill();
    }

    move(t) {
        const SCALE = 10;
        this.x += this.dx * SCALE * t / 1000;
        this.y += this.dy * SCALE * t / 1000;
        this.z += this.dz * SCALE * t / (5 * 1000);

        if (this.x < 0) {
            this.x = 0;
            this.dx = -this.dx; // Rework to do physic stuff here. or just regenerate?
        } else if (this.x > this.canvasWidth) {
            this.x = this.canvasWidth;
            this.dx = -this.dx;
        }

        if (this.y < 0) {
            this.y = 0;
            this.dy = -this.dy;
        } else if (this.y > this.canvasHeight) {
            this.y = this.canvasHeight;
            this.dy = -this.dy;
        }

        if (this.z < 0) {
            this.z = 0;
            this.dz = -this.dz;
        } else if (this.z > 100) {
            this.z = 100;
            this.dz = -this.dz;
        }
    }
}

const generateDots = (w, h) => {
    for(let i=0;i<50;i++) {
        dots.push(new Dot(w, h));
    }
}

const HeroAnimation = () => {

    useEffect(() => {
        drawOnCanvas();
    });

    return <Canvas />
}

export { HeroAnimation };